<script>
import aftersalesprofit from "@/components/element/report/aftersalesprofit/aftersalesprofit.vue"; //三包销售明细表
import sluggishstock from "@/components/element/report/sluggishstock/sluggishstock.vue"; //库存呆滞
import inventoryOccupation from "@/components/element/report/sluggishstock/inventoryOccupation.vue"; //库存占用明细表
