import verificationDetails from "@/components/element/report/verificationDetails/verificationDetails.vue"; //厂家核销明细表
import inventoryAgingReport from "@/components/element/report/inventoryAgingReport/inventoryAgingReport.vue"; //库龄分析汇总表
import inventoryAgingReportDetailed from "@/components/element/report/inventoryAgingReportDetailed/inventoryAgingReportDetailed.vue"; //库龄分析明细表
import warehouseTurnover from "@/components/element/report/warehouseTurnover/warehouseTurnover.vue"; //仓库周利率分析
import inventoryDOTReport from "@/components/element/report/inventoryDOTReport/inventoryDOTReport.vue"; // 库存DOT汇总表
import CompanyInventoryreport from "@/components/element/report/CompanyInventoryreport/index.vue"; // 公司级商品库存查询表

