<template>
  <div>
    <keep-alive :include="getKeepLiveBox">
      <router-view></router-view>
    </keep-alive>
    <iframeRouterView />
  </div>
</template>

<script>
// import { mapGetters } from "vuex";
import iframeRouterView from "@/components/element/report/iframe-router-view.vue";

export default {
  components: { iframeRouterView },
  data() {
    return {
      getKeepLiveBox: [],
    };
  },
  methods: {
    initRoute(route) {
      if (this.$mainProps) {
        this.$mainProps.microStore.commit("taspsChange", {route, base: this.$mainProps.routerBase}); // 添加到导航栏
        this.getKeepLiveBox = this.$mainProps.microStore.getters.getKeepLiveBox;
      }
    },
  },
  watch: {
    $route(next) {
      this.initRoute(next);
    },
  },
  mounted() {
    this.initRoute(this.$route);
  },
};
</script>

